import React, {Component} from 'react';
import LazyLoad from 'react-lazy-load';
import bats from './bats.json'


const aboutList = bats;
class About extends Component {
    state = {showButton: true}
    showAllItems = () => {
        console.log('hallo')
        aboutList.forEach(function (obj) {
            obj.show = true;
        });
        this.setState({showButton: false});
    }

    render() {
        return(
            <div className="row">
                 <LazyLoad height={300} offsetTop={10} onContentVisible={() => console.log('look ma I have been lazyloaded!')}>
                <div className="col-md-10 col-md-offset-1">
                    <ul className="timeline">
                         {aboutList.map((object, index) => 
                            {if(object.show){
                        return <li>
                            <div className="tl-icon">
                                <i className="fa fa-briefcase icon"></i>
                            </div>
                            <div className="timelabel">
                                <h3>{object.title}</h3>
                                <div className="date">
                                    <i className="fa fa-calendar"></i>
                                    {object.date}
                                </div>
                                <h4>{object.subTitle}</h4>
                                <p>{object.detail}</p>
                            </div>
                        </li>}return null}
                         )}
                    </ul>
                      {this.state.showButton ? (
                        <button className="btn btn-orange" onClick={()=>this.showAllItems()} >Load more</button>
                        ) : (
                            ('')
                    )}
                </div>
                </LazyLoad>
            </div>
        )
    }
}

export default About;


